import React from "react";
import { Navbar, Nav } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./header.css";

const Header = () => {
  return (
    <>
      <Navbar id="navbar" bg="myGray" expand="lg">
        <Link to={"/"}>
          <Navbar.Brand className="d-flex">
            <img
              id="logo"
              className="logo"
              alt=""
              src="/assets/images/logo5.svg"
            />
          </Navbar.Brand>
        </Link>
        <Navbar.Toggle
          className="bg-light ml-auto"
          aria-controls="basic-navbar-nav"
        />
        <Navbar.Collapse className="ml-auto" id="basic-navbar-nav">
          <Nav className="ml-auto ">
            <Nav.Link id="text-link" className="nav-link mx-auto text-light">
              <Link className="text-light" to={"/"}>
                Home
              </Link>
            </Nav.Link>
            <Nav.Link id="text-link" className="mx-auto text-light">
              <Link className="text-light" to={"/portofolioWorking"}>
                Work
              </Link>
            </Nav.Link>
            <Nav.Link id="text-link" className="mx-auto text-light">
              <Link className="text-light" to={"/portofolioHobby"}>
                Hobby
              </Link>
            </Nav.Link>
            <Nav.Link id="text-link" className="mx-auto text-light">
              <Link className="text-light" to={"/about"}>
                About
              </Link>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
};

export default Header;
