import React from "react";
import ReactPlayer from "react-player";
import videoBackground from "../../videosBackground/OVERTHINKING.mp4";

import "./video.css";

const videoplayer = () => {
  return (
    <div className="player-wrapper">
      <ReactPlayer
        url={videoBackground}
        width="100%"
        height="100%"
        loop={true}
        controls={true}
        playing={true}
        muted={true}
        light={false}
      />
    </div>
  );
};

export default videoplayer;
